	module.exports = {"data":{"markdownRemark":{"html":"<h2>Spikes</h2>\n<p>Not everything your team works on during the Sprint are User Stories. Sometimes, a user story cannot be effectively estimated or research is required. Maybe help is needed to decide between different architectures, to rule out some risks, or it may be a Proof of Concept (POC) to demonstrate a capability.</p>\n<p>In these cases, a Spike is added to the Sprint Backlog. The Acceptance Criteria for the Spike should be an answer to the question being posed. If the experiment is more difficult than anticipated, then maybe the answer is negative. (That’s why the Spike should be limited in time.) </p>\n<p>The time and energy invested in the Spike are intentionally limited so that the work can be completed within the Sprint while other User Stories are minimally impacted. The Spike does not usually receive a Story Point Estimate, but is given a fixed number of hours to be worked on.</p>\n<p>Demonstrate the results of the Spike in the Sprint Review. Based on the new information, the original question is reframed as a new User Story in a future Sprint.</p>\n<p>Your team gained the information necessary to make better decisions; to reduce uncertainty. With a small, measured amount of its resources to increase the quality and value of work in future Sprints, as opposed to making decisions based on guess-work.</p>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ul>\n<li>Mountain Goat Software <a href=\"https://www.mountaingoatsoftware.com/blog/spikes\">Spikes</a></li>\n<li>Scrum.org Forum <a href=\"https://www.scrum.org/forum/scrum-forum/5512/how-integrate-spike-scrum\">How to Integrate a Spike</a></li>\n<li>Scrum Alliance <a href=\"https://www.scrumalliance.org/community/articles/2013/march/spikes-and-the-effort-to-grief-ratio\">Spikes and Effort-to-Grief Ratio</a></li>\n<li>Wikipedia <a href=\"https://en.wikipedia.org/wiki/Spike_(software_development)\">Spike</a></li>\n</ul>","fields":{"slug":"/agile/spikes/"},"frontmatter":{"title":"Spikes"}}},"pathContext":{"slug":"/agile/spikes/"}}

